.list-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: max-content;
  height: max-content;
}

.list-container {
  display: flex;
  flex-wrap: wrap;
  max-width: 560px;

  & > :nth-child(3n) {
    margin-right: 0;
  }
}

.pagination-background {
  box-sizing: border-box;
  border-radius: 20px;
  background-color: white;
  z-index: 3;
}
