@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&family=Roboto:wght@400;700&display=swap);
body{margin:0;font-size:18px;font-family:'Inter', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.app-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100vw;height:100vh}.app-container::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-image:url(/react_node-superheroes/static/media/comics_wallpaper.5c7b6d3f.jpg);background-repeat:no-repeat;background-size:cover;background-position:center;opacity:0.6}

.add-form-container{width:400px}.add-form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.add-form_field{width:100%;margin-bottom:30px !important}.file-input{cursor:pointer;margin-bottom:20px}

.hero-card{box-sizing:border-box;position:relative;height:250px;width:180px;border-radius:15px;border:1px solid azure;margin-right:10px;margin-bottom:10px;overflow:hidden;transition:-webkit-transform 0.3s ease;transition:transform 0.3s ease;transition:transform 0.3s ease, -webkit-transform 0.3s ease}.hero-card_image{width:100%;object-fit:cover;height:100%}.hero-card_title{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;position:absolute;bottom:10px;left:10px;padding:0 5px;color:black;font-weight:bold;z-index:0}.hero-card_title::after{position:absolute;content:'';top:0;left:0;width:100%;height:100%;background-color:rgba(255,255,255,0.7);border-radius:5px;z-index:-1}.hero-card:hover{cursor:pointer;-webkit-transform:scale(1.05);transform:scale(1.05)}

.list-wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:-webkit-max-content;width:max-content;height:-webkit-max-content;height:max-content}.list-container{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;max-width:560px}.list-container>:nth-child(3n){margin-right:0}.pagination-background{box-sizing:border-box;border-radius:20px;background-color:white;z-index:3}

.info-container{max-width:600px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.info-container_main{display:-webkit-flex;display:flex;margin-bottom:20px}.info-container_main_photo{width:200px;height:200px;overflow:hidden;border-radius:8px;margin-right:20px}.info-container_main_photo_img{width:100%;height:100%;object-fit:cover}.info-container_main_info{width:70%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.info-container_gallery_title{display:inline-block;margin-bottom:10px}.info-container_gallery_photos{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.info-container_gallery_photos_img-container{position:relative;width:100px;height:100px;margin:5px;border-radius:8px;overflow:hidden}.info-container_gallery_photos_img-container:hover{cursor:pointer}.info-container_gallery_photos_img{width:100%;height:100%;object-fit:cover}.modal-photo{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:700px;height:600px}.modal-photo_image{object-fit:cover;width:100%;height:100%}.button-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}#file-input{opacity:0;position:absolute;z-index:-1}

